.container {
    padding: 0px 0px;
    margin-bottom: 70px;
  }
  
  .heading {
    color: #4070f4;
    text-align: center;
  }
  .skillContainer{
    display:flex;
    justify-content: space-around;
  }
 
  .tech,.tools{
    display: grid;
    grid-template-columns:1fr 1fr;
    gap:20px;
    margin-top: 10%;
    
  }
/* img{
  width:40%
} */
  .techSkills,.toolSkills{
    width:40%;
  }
  /* .tools{
    padding: 4%;
  } */

  @media only screen and (min-width: 0px) and (max-width: 1090px) {
 
    .skillContainer{
      display:flex;
      flex-direction: column;
      justify-content: space-around;
      /* border:1px solid red; */
      align-items: center;
    }

    .techSkills,.toolSkills{
      width:60%;
      /* border:1px solid; */
      padding-bottom: 2%;
    }
    .tech,.tools{
      display: grid;
      grid-template-columns:1fr 1fr;
      gap:30px;
      margin-top: 10%;
      
    }
    .skillContainer img{
      /* border:1px solid ; */
      width: 50%;
      height:auto;
    }
  }