.container {

    display: flex;
    padding: 10px 0%;
   
   
  }

  .logoImage{
    display: block;
    margin:auto;
    width:40%;
    height:auto;
  }
  
  .header{
    width: 50%;
    padding:0%;
    /* border:1px solid green; */
    font-size: 25px;
    line-height: 40px;
    font-weight: 600;
    /* border:1px solid red; */
    }
    .image{
    width:30%;
    margin-top: 8%;
    /* height: 60%; */
    border-radius: 50%;
    
    }
    .Img{
      
    width:85%;
    height:90%;
    border-radius: 50%;
    border:3px solid;
    }
    
    .homeHeader{
    /* background-color: white; */
    padding: 0% 0%;
    width: 60%;
    display: flex;
    justify-content:space-evenly;
    align-items:center;
    border-radius: 1%;
    /* border:1px solid red */
    }
    
    .button{
    /* border: 1px solid red; */
    display: flex;
     width:70%;
     margin: auto;
     justify-content: space-evenly;
    /* margin-top: 5%; 
    margin-left: 1%; */
    }
    
  

   
    
    

    

  
