    @media only screen and (min-width: 711px) and (max-width: 1090px) {
   
      /* border:3px solid; */
      .container {

        display: flex;
        padding: 10px 0%;
        flex-direction: column-reverse;
        /* border:1px solid red */
       
      }
      .homeHeader{
        /* background-color: white; */
        padding: 0% 0%;
        width: 60%;
        display: flex;
        justify-content:space-evenly;
        align-items:center;
        border-radius: 1%;
        /* border:1px solid red; */
        margin: auto;
       
        }

        .image{
          width:50%;
          margin-top: 8%;
          /* height: 60%; */
          border-radius: 50%;
          margin: auto;
          
          }
          .Img{
            
          width:85%;
          height:90%;
          border-radius: 50%;
          border:3px solid;
          }
          .header{
            width: 100%;
            padding:0%;
            /* border:1px solid green; */
            font-size: 30px;
            line-height: 40px;
            font-weight: 600;
            /* border:1px solid red; */
            }

            .logoImage{
              display: block;
              margin:auto;
              width:30%;
              height:auto;
            }
    }

