.container {
    margin-top: 0px;
    padding: 20px 5%;
    height: max-content;
    /* border: 1px solid ; */
  }
  
  .heading {
    color: #4070f4;
    text-align: center;
  }
  
  .projects{
    width:85%;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows:auto auto;
    margin-top: 2%;
    justify-content: space-around;
    align-items:center;
    /* border: 1px solid red; */
    gap: 80px;
    margin: auto;
  }

  .projects>div,.project>div{
    /* width:100%; */
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 1%;
    /* border:1px solid; */
   
  }
  .projects>div:hover{

  }

  .projectImg{
    width:100%;
    height:auto;
  }
  .projects a,.project a{
    text-decoration: none;
    color:black
  }

  .projectButton{
    width:60%;
    margin: auto;
    display: flex;
    justify-content: space-around;
  }
  .projectButton>button{
    background-color: #4070f4;
    color:white;
    padding:3% 5%;
    border:none;
    border-radius:3px;
    margin-bottom:4%
  }
  .emptyDiv{
    padding: 0%;
    box-shadow: none;
    display: none;
  }
  .project{
    width:100%;
    /* display:flex; */
    /* margin-top: 2%;
    justify-content: space-around; */
    align-items:center;
    border: 1px solid red;
  }

  @media only screen and (min-width: 0px) and (max-width: 1090px) {
    .projects{
      width:100%;
      display:flex;
      flex-direction: column;
      justify-content: space-around;
      align-items:center
    }
    
    .projects>div{
      width:60%;
      height: auto;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
     margin-bottom: 5%;
    }
  }
  h3+h4{
    margin: 1%;
  }

  @media only screen and (min-width: 0px) and (max-width: 961px) {
  
    .projects>div{
      width:90%;
      height: auto;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
     margin-bottom: 5%;
    }
  }