.App {
  text-align: center;
  /* background-color: #EEEEEE; */
  background: linear-gradient(90deg, rgba(255,255,255,1) 32%, rgba(204,239,255,1) 100%);  /* background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(70,205,252,1) 100%); */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
