
.contactContainer{
  display: flex;
  justify-content: space-evenly;
  width:80%;
  margin: auto;
  padding-bottom: 3%;
}
.conForm{
  color: black;
  background-color: white;
  margin-top: 20%;
  padding: 5% 10%;
  border-radius: 5px;
  font-weight: bold;
  
}
.con1{
  text-align: left;
  color: white;
}

.mainContactContainer{
  background-color: #06102D;
  padding-bottom: 1%;
}

.contactHeading{
  text-align: center;
}
.social>a{
  color:white;
 font-size: 30px;
 margin-right: 10%;
}
.social{
  display: flex;
  /* border:1px solid red; */

}
.con2{
  height: auto;
  width:auto;
  width:30%
 
}

.copyRight{
  color:white;
  margin-top: 0px;
  
}
.conForm>input{
  margin-bottom: 4%;
  padding:2% 3%
}
.sub{
  background-color: #4070F4;
  color:white;

  border: none;
  border-radius: 5px;
}

@media only screen and (min-width: 321px) and (max-width: 950px) {
  .contactContainer{
    display: flex;
    justify-content: space-evenly;
    width:80%;
    margin: auto;
    padding-bottom: 3%;
    flex-direction: column;
    align-items: center;
  }
  .con2{
    height: auto;
    width:auto;
    width:50%;
   
  }
  .conForm>input{
    margin-bottom: 4%;
    padding:2% 2%
  }
}
@media only screen and (min-width: 0px) and (max-width: 710px) {
  .con2{
    height: auto;
    width:auto;
    width:60%;
   
  }
  .conForm>input{
    margin-bottom: 4%;
    padding:2% 4%
  }

}

@media only screen and (min-width: 0px) and (max-width: 600px) {
  .con2{
    height: auto;
    width:auto;
    width:90%;
   
  }
  .conForm>input{
    margin-bottom: 4%;
    padding:2% 4%
  }
}
